body { width: 100%; height: 100vh; }

main { overflow: hidden; width: 100%; height: 100%; background-repeat: no-repeat; background-image: url(./../img/mobilebaypicboat-sm.png); background-size: cover; }

@media screen and (min-width: 768px) { main { background-repeat: no-repeat; background-image: url("./../img/dekstopbaypicboat-sm.png"); background-size: cover; } }

@media screen and (min-width: 1024px) { main { background-repeat: no-repeat; background-image: url("./../img/dekstopbaypicboat-sm.png"); background-size: cover; } }

main .content { height: 100%; position: relative; margin: 0 auto; padding-top: 57px; padding-bottom: 73px; display: flex; flex-direction: column; justify-content: space-between; max-width: 480px; object-fit: contain; }

main .content .img-logo { margin: 0 auto; width: 224px; object-fit: contain; }

main .content .img-logo img { width: 100%; height: auto; }

@media screen and (min-width: 768px) { main .content .img-logo { position: absolute; left: 4%; width: 250px; margin-right: auto; margin: unset; } }

@media screen and (min-width: 1028px) { main .content .img-logo { left: 5%; width: 310px; } }

@media screen and (min-width: 1440px) { main .content .img-logo { left: 5%; width: 310px; } }

main .content .img-text { height: 184px; width: 93.5%; margin: 0 auto; display: flex; justify-content: center; align-items: center; border: 3px solid white; }

main .content .img-text h1 { font-family: "BodoniSevITC-Book"; font-size: 64px; font-weight: normal; font-stretch: normal; font-style: normal; line-height: 0.71; letter-spacing: 1.14px; text-align: center; color: #ffffff; }

@media screen and (min-width: 1028px) { main .content .img-text { width: 620px; height: 380px; }
  main .content .img-text h1 { font-size: 120px; font-weight: normal; font-stretch: normal; font-style: normal; line-height: 0.94; letter-spacing: 2.14px; text-align: center; } }

@media screen and (min-width: 768px) { main .content .img-text { position: relative; top: 25vh; height: 315px; width: 470px; margin: 0 auto; border: 10px solid white; }
  main .content .img-text h1 { font-size: 95px; font-weight: normal; font-stretch: normal; font-style: normal; line-height: 0.71; letter-spacing: 2.86px; } }

@media screen and (min-width: 1440px) { main .content .img-text { width: 750px; height: 460px; }
  main .content .img-text h1 { font-weight: normal; font-stretch: normal; font-style: normal; line-height: 0.71; letter-spacing: 2.86px; text-align: center; color: #ffffff; font-size: 160px; } }

main .content .img-info { margin: 0 auto; padding: 0 6px; }

main .content .img-info p { margin: unset; font-family: "Helvetica"; font-size: 16px; font-weight: 300; font-stretch: normal; font-style: normal; line-height: normal; letter-spacing: 0.29px; text-align: center; color: #484848; }

main .content .img-info p a { color: inherit; text-decoration: none; }

@media screen and (max-width: 480px) { main .content .img-info p { color: #ffffff; } }

@media screen and (min-width: 768px) { main .content .img-info { position: absolute; right: 4%; margin: unset; margin-left: auto; }
  main .content .img-info p { font-size: 16px; font-weight: normal; font-stretch: normal; font-style: normal; line-height: normal; letter-spacing: 0.57px; text-align: left; } }

@media screen and (min-width: 1028px) { main .content .img-info { right: 5%; }
  main .content .img-info p { text-align: left; font-size: 24px; font-weight: 300; font-stretch: normal; font-style: normal; line-height: normal; letter-spacing: 0.43px; color: #484848; margin: 0; } }

@media screen and (min-width: 1440px) { main .content .img-info p { font-size: 24px; font-weight: 300; font-stretch: normal; font-style: normal; line-height: normal; } }

@media screen and (min-width: 768px) { main .content { max-width: 1440px; flex-direction: row; } }

@media screen and (max-height: 670px) and (max-width: 480px) { main .content { padding-bottom: 15px; } }

@media screen and (min-width: 768px) and (max-height: 550px) { main .content { padding-top: 10px; }
  main .content .img-text { height: 200px; top: 40vh; }
  main .content .img-text h1 { font-size: 100px !important; }
  main .content .img-logo { width: 205px; }
  main .content .img-info p { font-size: 21px; } }

@media screen and (max-height: 350px) { main .content { padding-top: 20px; padding-bottom: unset; } }

@font-face { font-family: "BodoniSevITC-Book"; src: url("../fonts/BodoniSevITC-Book.ttf") format("truetype"); font-weight: 400; font-style: normal; }

@font-face { font-family: "Helvetica"; src: url("../fonts/Helvetica-Light.ttf") format("truetype"); font-weight: 300; font-style: normal; }


